<template>
  <div>
      <PostTweet @change-show-tweet="showPostTweet = false" :showPostTweet="showPostTweet" v-if="showPostTweet == true"/>
      <button v-if="showPostTweet==false" @click="showPostTweet=true">Create tweet</button>
      <h1>Welcome to Homepage</h1>
  
        
      <ul v-if="tweets !== null">
        <li v-if="tweets[0] !== null">
          <h4>{{tweets[0].tweet}}</h4>        
          <h5>User: {{tweets[0].username}}</h5>
            <h5>Time: {{tweets[0].time}}</h5>
            <br>
        </li>
        <li v-if="tweets[1] !== null">
          <h4>{{tweets[1].tweet}}</h4>        
          <h5>User: {{tweets[1].username}}</h5>
            <h5>Time: {{tweets[1].time}}</h5>
            <br>
        </li>
        <li v-if="tweets[2] !== null">
          <h4>{{tweets[2].tweet}}</h4>        
          <h5>User: {{tweet[2].username}}</h5>
            <h5>Time: {{tweets[2].time}}</h5>
            <br>
        </li>
        <li v-if="tweets[3] !== null">
          <h4>{{tweet[3].tweet}}</h4>        
          <h5>User: {{tweets[3].username}}</h5>
            <h5>Time: {{tweets[3].time}}</h5>
            <br>
        </li>
        <li v-if="tweets[4] !== null">
          <h4>{{tweets[4].tweet}}</h4>        
          <h5>User: {{tweets[4].username}}</h5>
            <h5>Time: {{tweets[4].time}}</h5>
            <br>
        </li>
        <li v-if="tweets[5] !== null">
          <h4>{{tweets[5].tweet}}</h4>        
          <h5>User: {{tweets[5].username}}</h5>
            <h5>Time: {{tweets[5].time}}</h5>
            <br>
        </li>
        <li v-if="tweets[6] !== null">
          <h4>{{tweets[6].tweet}}</h4>        
          <h5>User: {{tweets[6].username}}</h5>
            <h5>Time: {{tweets[6].time}}</h5>
            <br>
        </li>
        <li v-if="tweets[7] !== null">
          <h4>{{tweets[7].tweet}}</h4>        
          <h5>User: {{tweets[7].username}}</h5>
            <h5>Time: {{tweets[7].time}}</h5>
            <br>
        </li>
        <li v-if="tweets[8] !== null">
          <h4>{{tweets[8].tweet}}</h4>        
          <h5>User: {{tweets[8].username}}</h5>
            <h5>Time: {{tweets[8].time}}</h5>
            <br>
        </li>
        <li v-if="tweets[9] !== null">
          <h4>{{tweets[9].tweet}}</h4>        
          <h5>User: {{tweets[9].username}}</h5>
            <h5>Time: {{tweets[9].time}}</h5>
            <br>
        </li>
      </ul>
      <button @click="nextPage()"> Next Page </button>
          current page: {{page/5+1}}
      <button @click="logOut()">Log Out</button>
  </div>
</template>

<script>
import axios from 'axios';
import PostTweet from "../components/PostTweet.vue"

export default {
    name: "Home",
    data(){
        return {
            tweets: null,
            page: 0,
            showPostTweet: false
        }
    },
    components:{
        PostTweet
    },
    created () {
        const token = localStorage.getItem('token')
        if (token) {
        axios.defaults.headers.common['Authorization'] = "Bearer " + token
        } else {
            this.$router.push('/login')
        }
        axios.get(`/tweets/tweetsofmany?page=${this.page}`)
        .then((res) => {
            this.tweets = res.data.tweetsOfMany
            console.log(res.data)
            if(res.data.message == "error occured at verification"){
                this.$router.push('/login')
            }
        })
        .catch((err) => {
            console.log(err)
        })
    },
    methods: {
        logOut(){
            localStorage.removeItem('token')
            axios.defaults.headers.common['Authorization'] = "Bearer " +"null"
            this.$router.push('/login')
        },
        nextPage(){
            this.page += 5;
            axios.get(`/tweets/tweetsofmany?page=${this.page}`)
            .then((res) => {
                this.tweets = res.data
            })
            .catch((err) => {
                console.log(err)
            })
        }
    }

}
</script>

